/* unplugin-vue-components disabled */import store from '../store/index';
import api from '../api/index';
export default {
  name: 'Mylocation',

  data() {
    return {
      isAdding: false,
      locations: [],
      newLocstring: ''
    };
  },

  methods: {
    addNewLoc() {
      console.log("adding new locations");

      if (this.locations.length < 5) {
        this.isAdding = true;
      } else {
        ElMessage.error('At most 5 locations');
      }
    },

    confirmAdd() {
      console.log('new location is ' + this.newLocstring);
      var emial = store.getters.getEmail;
      var locInfo = {
        locationid: -1,
        email: emial,
        address: this.newLocstring
      };
      var that = this;
      api.addLocation(locInfo).then(res => {
        console.log(res.data); // that.locations.push(locInfo)

        that.clsoeNewLocBar(); // that.newLocstring = ''
      });
      this.clsoeNewLocBar();
      this.locations.push(locInfo);
      this.newLocstring = '';
    },

    clsoeNewLocBar() {
      console.log("close new loc bar");
      this.isAdding = false;
    },

    deleteLoc(index) {
      console.log("Delete location " + this.locations[index].address);
      var locInfo = this.locations[index];
      var that = this; // api.deleteLocation(locInfo).then(res => {
      //   console.log(res.data)
      //   that.locations.splice(index, 1)
      // })

      this.locations.splice(index, 1);
    }

  },

  created() {
    this.locations = store.getters.getLocations;
  }

};